body {
  min-width: 320px;
  margin: 0;
  font-family: 'Tahoma', sans-serif;
  color: #fff;
  box-sizing: border-box;
}
main {
  height: 100vh;
}
/*
LESS variables are information about icon's compiled state, stored under its original file name

