.page-header {
  color: #fff;
  background-color: #54add8;
  position: fixed;
  width: 100%;
  z-index: 100000;
  padding-right: 2px;
}
.page-header__container {
  display: flex;
  justify-content: space-between;
}
.container {
  max-width: 1200px;
  margin: 0 auto;
  padding-left: 16px;
  padding-right: 16px;
}
.menu {
  flex: 760;
  z-index: 100;
  margin-top: 0;
  margin-bottom: 0;
}
.menu__list {
  display: flex;
  position: relative;
  justify-content: space-around;
  margin: 0;
  padding-left: 0;
  list-style: none;
}
.menu__list-item {
  position: relative;
  height: 100%;
  padding-top: 14px;
  padding-right: 8px;
  padding-bottom: 12px;
  padding-left: 8px;
}
.menu__list-item--multi {
  padding-right: 25px;
}
.menu__list-link-icon {
  position: absolute;
  top: 7px;
  right: -15px;
  background-image: url(../img/sprite-003725742166288293.png);
  background-position: -54px -42px;
  width: 10px;
  height: 5px;
}
.menu__list-link {
  position: relative;
  text-transform: uppercase;
  text-decoration: none;
  color: #fff;
  font-size: 14px;
  font-weight: bold;
}
.menu__list-link:hover {
  transition: color 0.2s linear;
  color: #fbb04c;
}
.menu__list-link:focus {
  transition: color 0.2s linear;
  color: #fbb04c;
}
.menu__list-link--active {
  color: #fbb04c;
}
.menu__submenu-list {
  display: none;
  border-radius: 0 0 6px 6px;
  padding-left: 0;
  list-style: none;
}
.menu__submenu-item {
  width: 160px;
}
.menu__submenu-list--active {
  display: block;
  position: absolute;
  top: 44px;
  left: 0;
}
.menu__submenu-link {
  display: block;
  background-color: #54add8;
  padding-top: 8px;
  padding-bottom: 5px;
  padding-left: 10px;
  transition: background-color 0.2s linear;
  text-decoration: none;
  color: #fff;
  font-size: 14px;
}
.menu__submenu-link:hover {
  background-color: #bfd8eb;
  transition: background-color 0.2s linear;
  color: #fff;
}
.menu__show-icon {
  position: absolute;
  top: 8px;
  left: 6px;
}
.menu__close-icon {
  position: absolute;
  top: 6px;
  left: 6px;
}
.sign-in {
  position: relative;
  flex: 74;
  padding-top: 14px;
  padding-right: 13px;
  padding-bottom: 8px;
  text-align: right;
}
.sign-in__link {
  opacity: 0.5;
  transition: opacity 0.2s linear;
  color: #fff;
  font-size: 14px;
}
.sign-in__link:hover {
  opacity: 1;
  transition: opacity 0.2s linear;
  color: #fff;
}
.sign-in__icon {
  display: inline-block;
  position: absolute;
  top: 15px;
  right: 63px;
  background-repeat: no-repeat;
  background-image: url(../img/sprite-003725742166288293.png);
  background-position: -24px -27px;
  width: 12px;
  height: 15px;
}
@media (max-width: 480px) {
  .sign-in {
    flex: 170;
  }
}
@media (max-width: 768px) {
  .sign-in {
    flex: 170;
  }
}
@media (max-width: 992px) {
  .menu__list-item:first-child {
    padding-left: 0;
  }
  .menu__list {
    flex-direction: column;
    justify-content: flex-start;
    padding-top: 40px;
  }
  .menu__submenu-list {
    position: absolute;
    z-index: 100000000000000000;
    background-color: #54add8;
  }
  .menu__submenu-list--active {
    top: 40px;
    background-color: #54add8;
  }
  .menu__submenu-item {
    width: 100%;
    border: none;
    background-color: #54add8;
  }
  .menu__submenu-link {
    width: 100%;
    background-color: #54add8;
    color: #fff;
  }
  .menu__list-item:hover {
    background-color: #54add8;
    transition: background-color 0.2s linear;
  }
  .menu__list-item:focus {
    background-color: #54add8;
    transition: background-color 0.2s linear;
  }
  .menu__list-item--active {
    background-color: #54add8;
  }
}
@media (max-width: 1080px) {
  .menu__submenu-list {
    position: absolute;
    z-index: 100000000000000000;
    background-color: #54add8;
  }
  .menu__submenu-list--active {
    top: 40px;
    background-color: #54add8;
  }
  .menu__submenu-item {
    width: 100%;
    border: none;
    background-color: #54add8;
  }
  .menu__submenu-link {
    width: 100%;
  }
}
@media screen and (max-width: 1080px) {
  .menu__submenu-link {
    background-color: #54add8;
    color: #fff;
  }
  .menu__list-link-icon {
    left: 145px;
  }
}
.input-block {
  background-color: #0085c5;
  text-align: center;
  padding-top: 46px;
  padding-bottom: 38px;
  box-sizing: border-box;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.input-block__container {
  width: 100%;
  box-sizing: border-box;
}
.main-title {
  margin-top: 0;
  margin-bottom: 41px;
  font-weight: 400;
  font-size: 2.5rem;
}
.input-city {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 22px;
}
.input-city__item {
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: space-between;
  text-align: left;
}
.input-city__item--departure {
  margin-right: 2px;
}
.input-city__item--arrival {
  margin-left: 2px;
}
.input-city__item-title {
  position: relative;
  width: 100%;
  font-size: 14px;
}
.input-city__input-field {
  box-sizing: border-box;
  flex: 1;
  margin-top: 5px;
  margin-bottom: 5px;
  width: 100%;
  border: none;
  background-color: #ffffff;
  padding-top: 16px;
  padding-right: 14px;
  padding-bottom: 16px;
  padding-left: 14px;
  font-size: 17px;
}
.input-city__input-field--departure {
  border-top-left-radius: 6px;
  border-bottom-left-radius: 6px;
  padding-right: 40px;
}
.input-city__input-field--arrival {
  border-top-right-radius: 6px;
  border-bottom-right-radius: 6px;
}
.input-city__arrows {
  display: inline-block;
  position: absolute;
  top: 34px;
  left: -49px;
  z-index: 1;
  width: 40px;
  height: 40px;
  background-repeat: no-repeat;
}
.input-city__arrows--right {
  display: block;
  position: absolute;
  top: 0px;
  left: 10px;
  width: 20px;
  height: 8px;
  transition: left 0.2s linear;
  background-image: url(../img/sprite-003725742166288293.png);
  background-position: -27px 0px;
  width: 23px;
  height: 23px;
}
.input-city__arrows--left {
  display: block;
  position: absolute;
  top: 10px;
  left: 5px;
  width: 20px;
  height: 8px;
  transition: left 0.2s linear;
  content: "";
  background-image: url(../img/sprite-003725742166288293.png);
  background-position: 0px 0px;
  width: 23px;
  height: 23px;
}
.input-city__example {
  font-size: 14px;
}
.input-city__example-link {
  opacity: 0.5;
  text-decoration: none;
  color: #fff;
  transition: opacity 0.2s linear;
}
.input-city__example-link:hover {
  opacity: 1;
  transition: opacity 0.2s linear;
}
.input-city__example-text {
  opacity: 0.5;
}
@media (max-width: 992px) {
  .input-city {
    align-items: center;
  }
  .input-city__items {
    margin-bottom: 20px;
  }
}
@media (max-width: 480px) {
  .input-city__input-field {
    max-width: 250px;
    font-size: 16px;
  }
}
.date-block {
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 11;
}
.date-block__item {
  text-align: left;
}
.date-block__label {
  font-size: 14px;
}
.date-block__input {
  box-sizing: border-box;
  margin-top: 4px;
  border: 1px solid #d7d7d7;
  background-color: #ffffff;
  padding-top: 12px;
  padding-right: 25px;
  padding-bottom: 12px;
  padding-left: 14px;
  font-size: 17px;
}
.date-block__input--departure {
  border-top-left-radius: 6px;
  border-bottom-left-radius: 6px;
}
.date-block__input--arrival {
  border-top-right-radius: 6px;
  border-bottom-right-radius: 6px;
}
.date-block__input--inactive {
  border: 1px solid #0076ad;
  background-color: #1395d3;
}
.date-block__input--inactive::-webkit-input-placeholder {
  color: #94c4db;
}
.date-block__input--inactive::-moz-placeholder {
  color: #94c4db;
}
.date-block__input-wrapper {
  display: block;
  position: relative;
}
.date-block__date-icon {
  position: absolute;
  top: 12px;
  right: 15px;
  width: 28px;
  height: 28px;
  background-repeat: no-repeat;
  fill: #656565;
}
.date-block__date-icon--disabled {
  fill: #94c4db;
}
.date-block__roundtrip-label {
  position: relative;
  margin-top: 25px;
  margin-right: 5px;
  margin-bottom: 0;
  margin-left: 5px;
  border-radius: 6px;
  padding-top: 6px;
  padding-right: 10px;
  padding-bottom: 5px;
  padding-left: 6px;
  font-size: 14px;
}
.date-block__roundtrip-check {
  vertical-align: middle;
}
.date-block__roundtrip-check[type=checkbox] {
  transform: scale(1.4);
}
.ui-datepicker.ui-widget.ui-widget-content.ui-helper-clearfix.ui-corner-all {
  z-index: 100;
}
@media (max-width: 992px) {
  .date-block {
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 20px;
    width: 100%;
    padding-left: 0;
  }
  .date-block__item {
    flex: 6;
  }
  .date-block__input {
    width: 100%;
