@media screen and (max-width: 480px) {
  .date-block__input::placeholder {
    color: transparent;
  }
}
.input-date-currency {
  display: flex;
  align-content: center;
  justify-content: space-between;
}
@media (max-width: 992px) {
  .input-date-currency {
    align-items: center;
    flex-direction: column;
  }
  .currency {
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    align-items: center;
    flex-direction: column;
  }
}
.currency {
  display: flex;
  align-items: center;
  margin-left: 15px;
  text-align: left;
}
.currency__select {
  position: relative;
  margin-top: 5px;
  min-width: 85px;
  border: none;
  border-radius: 6px 6px;
  background-color: #fff;
  padding-top: 10px;
  padding-right: 15px;
  padding-bottom: 9px;
  padding-left: 11px;
  color: #000;
  font-size: 17px;
  font-weight: bold;
  -moz-appearance: none;
  -webkit-appearance: none;
}
.currency__select:active {
  border-radius: 6px 6px 0px 0px;
}
.currency__label {
  color: #fff;
  font-size: 14px;
}
.currency__wrapper {
  display: block;
  position: relative;
}
.currency__wrapper:after {
  position: absolute;
  top: 30px;
  right: 7px;
  content: '';
  pointer-events: none;
  background-image: url(../img/sprite-003725742166288293.png);
  background-position: 0px -27px;
  width: 20px;
  height: 26px;
}
.currency-button-wrapper {
  display: flex;
  justify-content: space-between;
  margin-left: 9px;
  width: 100%;
}
@media (max-width: 992px) {
  .currency {
    margin-right: 15px;
    margin-left: 0;
  }
  .currency-button-wrapper {
    margin-left: 0px;
  }
}
.search-button {
  position: relative;
  margin-top: 26px;
  margin-bottom: 0;
  margin-left: 15px;
  width: 100%;
  height: 43px;
  border: 1px solid #de5300;
  border-radius: 3px;
  box-shadow: inset 0 2px 0 #fcc272;
  background-image: linear-gradient(to top, #f75e02 0%, #fbb04c 100%);
  padding-top: 13px;
  padding-right: 25px;
  padding-bottom: 12px;
  padding-left: 55px;
  cursor: pointer;
  color: #fff;
  font-size: 16px;
}
.search-button__text {
  position: relative;
}
.search-button__text:after {
  display: block;
  position: absolute;
  top: 0px;
  left: -25px;
  content: "";
  background-image: url(../img/sprite-003725742166288293.png);
  background-position: -54px -20px;
  width: 18px;
  height: 18px;
}
@media (max-width: 768px) {
  .search-button {
    margin-top: 20px;
    margin-right: 0;
    margin-left: 0;
    width: 100%;
    max-width: none;
  }
}
@media (max-width: 992px) {
  .search-button {
    margin-top: 20px;
    margin-right: 0;
    margin-left: 0;
    max-width: none;
  }
}
