/* Overlays */
.ui-widget-overlay {
  background: #aaaaaa;
  opacity: .3;
  filter: alpha(opacity=30);
  /* support: IE8 */
}
.ui-widget-shadow {
  -webkit-box-shadow: 0px 0px 5px #666666;
  box-shadow: 0px 0px 5px #666666;
}
.response--main-navigation a {
  display: block;
  text-decoration: none;
}
.response--main-navigation li {
  list-style-type: none;
}
.response--site-menu {
  display: flex;
  justify-content: space-between;
}
/* Responsive! */
.show-nav,
.hide-nav {
  position: relative;
  width: 40px;
  height: 40px;
  cursor: pointer;
}
.hide-nav {
  margin-bottom: 2rem;
  width: 40px;
  height: 40px;
}
.response--main-navigation.mobile-nav .response--site-menu {
  display: block;
  top: 0px;
  width: 100%;
  min-height: 100vh;
  overflow-y: auto;
  transform: translateX(-100%);
  transition: all .05s;
}
.response--main-navigation.mobile-nav.expanded .response--site-menu {
  right: 0;
  transform: translateX(0);
}
@media screen and (max-width: 1080px) {
  button.show-nav {
    padding: 0;
  }
  .response--site-menu {
    display: none;
    position: absolute;
    top: 0px;
    left: 0px;
    z-index: 99999;
    width: 100%;
    min-height: 100vh;
    background-color: #54add8;
    padding-top: 5px;
    padding-right: 50px;
    padding-left: 20px;
  }
  .show-nav {
    border: none;
    background-color: transparent;
  }
  .hide-nav {
    position: relative;
    border: none;
    background-color: transparent;
    padding-left: 20px;
  }
}
.ui.segment {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100vh;
  margin-top: 0;
  margin-bottom: 0;
  z-index: 10000;
  border-radius: 0;
}
.ui.dimmer {
  background-color: #0085c5;
}
.tickets {
  display: flex;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  background-color: #0085c5;
}
.arrow-left {
  background-image: url(../img/sprite-003725742166288293.png);
